* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  border: 2px solid #ff4e97;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}
.state h1 {
  color: #ff4e97;
  font-size: 28px;
  margin-bottom: 10px;
}
.state img {
  width: 90%;
  height: 400px;
  object-fit: cover;
}
.state input {
  width: 50%;
  height: 40px;
  border-radius: 0;
  border: 5px solid #ff4e97;
  padding: 0 5px;
  text-align: center;
  font-size: 18px;
  color: #ff4e97;
  margin-top: 10px;
}
.state input::placeholder {
  color: #ff4e97;
}
.state button {
  width: 50%;
  height: 25px;
  background-color: #ff4e97;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border-radius: 0;
  border: 0;
  padding: 0 5px;
  margin-top: 5px;
}
.state p {
  width: 90%;
  height: 300px;
  background-color: #ffe2ee;
  padding: 20px;
  margin-top: 20px;
}
.state span {
  color: #ff4e97;
}
